*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Bebas Neue", cursive;
}

body {
  overflow-x: hidden;
  min-height: 100vh;
  background-color: #090E0E;
}

ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

button, input {
  background: transparent;
  border: none;
  outline: none;
}

.center-f {
  display: flex;
  align-items: center;
}

.center-g {
  display: grid;
  place-items: center;
}

.position-flow {
  position: absolute;
  top: 0;
  left: 0;
}

.smoothPointer {
  transition: 0.5s;
  cursor: pointer;
}

.container {
  padding: 0 15px;
  margin: 0 auto;
}

/* ------------------------- Start style Container ------------------------- */
.container {
  padding: 0 15px;
  margin: 0 auto;
}

/* Small */
@media (min-width: 768px) {
  .container {
    width: 750px;
  }
}
/* Medium */
@media (min-width: 992px) {
  .container {
    width: 970px;
  }
}
/* Large */
@media (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}
/* ------------------------- End style Container ------------------------- */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

* {
  scrollbar-color: var(--drb-Main-Color) transparent;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  width: 0.4em;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: var(--drb-Main-Color);
  border: 3px solid transparent;
  border-radius: 5px 0 0 5px;
}

.mobile-nav {
  padding: 1.5rem 1.875rem;
}
.mobile-nav .logo img {
  height: 2.25rem;
  filter: brightness(50);
}
.mobile-nav .bottom-nav {
  background-color: rgba(255, 255, 255, 0.0470588235);
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 0.5rem 0.5rem 0 0;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.mobile-nav .bottom-nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 1.5rem;
}
.mobile-nav .bottom-nav ul li {
  width: 40px;
}
.mobile-nav .bottom-nav ul li a {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mobile-nav .bottom-nav ul li a img {
  width: 2rem;
  transform: translateY(10px);
  filter: saturate(0);
  transition: 0.5s;
}
.mobile-nav .bottom-nav ul li a p {
  margin-top: 3px;
  font-size: 0.75rem;
  font-weight: 500;
  color: #38796b;
  letter-spacing: 3px;
  scale: 0;
  transition: 0.5s;
}
.mobile-nav .bottom-nav ul li.active img {
  filter: unset;
  transform: translateY(0px);
}
.mobile-nav .bottom-nav ul li.active p {
  scale: 1;
  display: inline-block;
}
.mobile-nav .bottom-nav ul .flow-bubbol {
  position: absolute;
  top: -20px;
  background: radial-gradient(#41c6c6, #235050);
  padding: 0.9rem 0.65rem;
  border-radius: 50%;
  box-shadow: 0 0 16px 16px rgba(83, 198, 198, 0.2117647059);
  animation: flowBubbol 4s infinite alternate;
  transition: 0.5s;
}

@keyframes flowBubbol {
  50% {
    box-shadow: 0 0 16px 5px rgba(83, 198, 198, 0.2117647059);
  }
  100% {
    box-shadow: 0 0 16px 16px rgba(83, 198, 198, 0.2117647059);
  }
